#quote-box {
    border: 1px solid #ccc;
    border-radius: 15px;
    border: none;
    color: black;
    display: flex;
    justify-content: space-between;
    height: 60vh;
    margin: 0 auto;
    margin-top: 10%;
    width: 50%; 
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}
.quote-text {
    width: 50%;
    margin-top: 18%;
}

.quote-image {
    width: 50%;
}
.quote-image img{
    width: 100% !important;
    height: 100% !important;
    object-fit: cover !important;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 10px; 
}

#text {
    font-size: 22px;
}

#author {
    font-size: 18px;
    font-style: italic;
    margin-top: 10px;
}

#new-quote, #tweet-quote {
    padding: 15px;
    border-radius: 5px;
    cursor: pointer;
    text-decoration: none;
    border: none;
    outline: none;
    color: rgb(255, 255, 255);
    background: #111;
    cursor: pointer;
    position: relative;
    z-index: 0;
    border-radius: 10px;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    margin: 5%;
}

#new-quote:hover, #tweet-quote:hover {
    transition: 1s all ease-in;
}

.button-85 {
  padding: 20px;
  border: none;
  outline: none;
  color: rgb(255, 255, 255);
  background: #111;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  margin: 5%;
}

.button-85:before {
  content: "";
  background: linear-gradient(
    45deg,
    #ff0000,
    #ff7300,
    #fffb00,
    #48ff00,
    #00ffd5,
    #002bff,
    #7a00ff,
    #ff00c8,
    #ff0000
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  -webkit-filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing-button-85 20s linear infinite;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}

@keyframes glowing-button-85 {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

.button-85:after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #222;
  left: 0;
  top: 0;
  border-radius: 10px;
}

a{
    background-color: black;
}